import React, { Component } from 'react';
import { Map, TileLayer, Circle, Polyline, Polygon } from 'react-leaflet';
import './App.css';

//const tileUrl = 'http://{s}.tile.osm.org/{z}/{x}/{y}.png';
const tileUrl = 'https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token=pk.eyJ1IjoibmF0dyIsImEiOiJjaWtrZzNsOTIwYjdrdWNqN2s1Y2g5Y3B0In0.ioaXcCRzjmSHpkaoQ4Oq4g';
const id = 'mapbox.satellite';
const accessToken = 'pk.eyJ1IjoibmF0dyIsImEiOiJjaWtrZzNsOTIwYjdrdWNqN2s1Y2g5Y3B0In0.ioaXcCRzjmSHpkaoQ4Oq4g';
const tileAttr = '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors';
const mapCenter = [49.299, -123.064];
const zoomLevel = 14;

function XButton(props) {
  const onClick = (props.onClick !== undefined) ? props.onClick : () => { console.log("click " + props.text) }
  return (
    <div className={"remove-btn"} id={"new-feature"} onClick={onClick} >
      {props.text}
    </div>
  )
}

function GeometryTypeSelector(props) {
  const geometryTypes = [
    "Point",
    "LineString",
    "Polygon"
  ];
  let options = geometryTypes.map((k, i) => {
    return <option value={k} key={i}>{k}</option>;
  })
  return (
    <select value={props.value} onChange={props.handleSelect} id={"geotype-selector"}>
      {options}
    </select>
  );
}

function FeatureRow(props) {
  const className = (props.idx === props.activeIdx) ? "feature-row selected-feature-row" : "feature-row"
  const onClick = props.handleClick(props.feature.geometry.type, props.idx);
  let featureLength = 0;
  switch (props.feature.geometry.type) {
    case "Polygon":
      featureLength = props.feature.geometry.coordinates[0].length
      break;
    default:
      featureLength = props.feature.geometry.coordinates.length;
  }
  return (
    <div className={className} onClick={onClick} >
      <div style={{"float": "left"}}>
        <p>{props.feature.geometry.type} {"("+featureLength+")"}</p>
      </div>
      <div style={{"float": "right"}}>
        <XButton text={"X"} onClick={props.deleter} />
      </div>
      <div  style={{"clear":"both"}}></div>
    </div>
  );
}

function FeatureList(props) {
  let features = props.features.map((f, i) => {
    return <FeatureRow
              feature={f}
              key={i}
              idx={i}
              activeIdx={props.activeIdx}
              handleClick={props.handleClick}
              deleter={ () => { props.deleteFunc(i) } }
            />;
  });
  return (
    <div id={"feature-list"}>
      <div className={"section-header"}><span className={"header-text"}>Features</span></div>
      {features}
    </div>
  )
}

function PropertyEditor(props) {
    if (props.feature === undefined) {
      return <div></div>;
    }

    const setProperty = (key, value, oldkey) => {
      if (key.length !== 0) {
        if (value.length === 0) {
          props.setProperty(key, "", oldkey);
        } else {
          props.setProperty(key, value, oldkey);
        }
      } else {
        props.setProperty("", "", oldkey);
      }
    };

    const p = props.feature.properties;
    let rows = Object.keys(p).map((k, i) => {
                  return <PropertyKeyValue name={k} value={p[k]} key={i} setProp={setProperty} />
               });
    rows.push(<PropertyKeyValue name={""} value={""} key={rows.length} setProp={setProperty} />);
    return (
      <div id="property-editor">
        <div className={"section-header"}><span className={"header-text"}>Properties</span></div>
        {rows}
      </div>
    );
}

function PropertyKeyValue(props) {
  const setValue = (event) => {props.setProp(props.name, event.target.value, props.name);};
  const setName = (event) => {props.setProp(event.target.value, props.value, props.name);};
  return (
    <div>
      <TextBox className="property-key" value={props.name} handleChange={setName} placeholder="Property name" />
      <TextBox className="property-value" value={props.value} handleChange={setValue} placeholder="Value" />
    </div>
  );
}

function TextBox(props) {
  const placeholder = (props.placeholder) ? props.placeholder : "";
  return (
    <input type="text" className={props.className} value={props.value} onChange={props.handleChange} placeholder={placeholder} />
  );
}

class SourceWindow extends Component {

  componentDidMount() {
    const Clipboard = require('clipboard');
    this.clipboard = new Clipboard("button");
  }

  componentWillUnmount() {
    this.clipboard.destroy();
  }

  render() {
    return (
      <div id="source-window">
        <div className={"section-header"}>
          <span className={"header-text"}>GeoJSON</span>
        </div>
        <pre className={"json-string"} id={"active-feature-json"}>
          {JSON.stringify(this.props.feature, correctGeoJson, 2)} 
        </pre>
        <button data-clipboard-target={"#active-feature-json"} className="clipboard-btn">
          <img src={"assets/clippy.svg"} alt={"copy to clipboard"} width={"18px"}/>
        </button>
        <div style={{"clear": "both"}}></div>
      </div>
    );
  }
}

// try to add a coordinate to a geometry in a way that makes sense:
//  point: convert to multipoint and append a new point
//  line: add a new vertex
//  polygon: add a new vertex after the last edited vertex
//  multipoint: append a new point
function geoAppend(geo, latlng) {
  let ret = geo;
  const xy = [latlng.lng, latlng.lat];
  const geotype = geo.type

  if ((geotype === "LineString") || (geotype === "MultiPoint")) {
    ret.coordinates.push(xy);
  } else if (geotype === "Polygon") {
    const L = geo.coordinates[0].length;
    ret.coordinates[0].splice(L-1, 0, xy);
  } else if (geotype === "Point") {
    if (geo.coordinates.length === 0) {
      ret.coordinates = xy;
    } else {
      ret.type = "MultiPoint"
      ret.coordinates = [ret.coordinates];
      ret = geoAppend(ret, latlng);
    }
  } else {
    throw new Error("unhandled type " + geotype);
  }
  return ret;
}

function geoIsValid(geo) {
  if ((geo.type === "Point") && (geo.coordinates.length < 2)) {
    return false;
  }
  return true;
}

// replacer function for JSON.stringify() to correct some of the more egregious
// GeoJSON violations
function correctGeoJson(key, geojson) {
  if (geojson.type === "undefined") {
    return geojson;
  }

  let ret;
  switch (geojson.type) {
    case "Feature":
      ret = {
        "type": "Feature",
        "geometry": correctGeoJson(geojson.geometry),
        "properties": geojson.properties
      };
      break;
    case "Polygon":
      ret = geojson;
      ret = {
        "type": "Polygon",
        "coordinates": geojson.coordinates.map((ring) => {
          let closedRing = ring.slice();
          if (closedRing[0] !== closedRing[closedRing.length]) {
            closedRing.push(closedRing[0]);
          }
          return closedRing;
        }),
      }
      break;
    default:
      ret = geojson;
  }
  return ret;
}

class App extends Component {

  constructor(props) {
    super(props);

    const features = (props.features && props.features.length !== 0) ? props.features : [];

    this.state = {
      "activeFeature": 0,
      "features": features,
      "nextGeometryType": "Polygon"
    }

    this.geometryType = this.geometryType.bind(this);
    this.activeFeature = this.activeFeature.bind(this);
    this.setNextGeometryType = this.setNextGeometryType.bind(this);
    this.geoJsonToLeaflet = this.geoJsonToLeaflet.bind(this);
    this.handleMapClick = this.handleMapClick.bind(this);
    this.handleFeatureClickFactory = this.handleFeatureClickFactory.bind(this);
    this.addFeature = this.addFeature.bind(this);
    this.deleteFeature = this.deleteFeature.bind(this);
    this.setFeatureProperty = this.setFeatureProperty.bind(this);
    this.getFeatureProperty = this.getFeatureProperty.bind(this);
  }

  geometryType() {
    if (this.state.features.length > this.state.activeFeature) {
      return this.state.features[this.state.activeFeature].geometry.type;
    } else {
      return "Polygon";
    }
  }

  activeFeature() {
    if (this.state.features.length > this.state.activeFeature) {
      return this.state.features[this.state.activeFeature];
    } else {
      return this.state.features[this.state.features.length-1];
    }
  }

  handleMapClick(e) {
    this.setState((prevState, props) => {
      let features = prevState.features.slice()
      features[prevState.activeFeature].geometry = geoAppend(features[prevState.activeFeature].geometry, e.latlng);
      return {"features": features};
    });
  }

  handleFeatureClickFactory(geometryType, featureIndex) {
    return (e) => {
      this.setState({
        "geometryType": geometryType,
        "activeFeature": featureIndex
      });
    }
  }

  setNextGeometryType(e) {
    this.setState({ "nextGeometryType": e.target.value });
  }

  addFeature() {
    this.setState((prevState, props) => {
      let features = this.state.features.slice();
      let newFeature = {"properties": {}, "geometry": {}}
      switch (this.state.nextGeometryType) {
        case "Point":
          newFeature.geometry = {"type": "Point", "coordinates": []};
          break;
        case "LineString":
          newFeature.geometry = {"type": "LineString", "coordinates": []};
          break;
        case "Polygon":
          newFeature.geometry = {"type": "Polygon", "coordinates": [[]]};
          break;
        default:
          throw new Error("unhandled geometry for creation: " + this.state.nextGeometryType);
      }
      features.push(newFeature);
      return {
        "features": features,
        "activeFeature": features.length - 1
      };
    });
  }

  deleteFeature(idx) {
    this.setState((prevState, props) => {
      if (idx >= prevState.features.lengths) {
        throw new Error("delete index out of bounds");
      }

      let features = prevState.features.slice();
      features.splice(idx, 1);
      return {
        "features": features,
        "activeFeature": Math.min(this.state.activeFeature, features.length - 1)
      };
    });
  }

  setFeatureProperty(idx, key, val, oldkey) {
    let features = this.state.features.slice();

    if (key.length === 0) {
      delete features[idx].properties[oldkey];
    } else {
      if (oldkey !== undefined && key !== oldkey) {
        delete features[idx].properties[oldkey]
      }
      features[idx].properties[key] = val;
    }
    this.setState({ "features": features });
  }

  getFeatureProperty(idx, name) {
    return this.state.features[idx].properties[name];
  }

  deleteFeatureProperty(idx, propname) {
    let features = this.state.features.slice();
    delete features[idx].properties[propname];
    this.setState({ "features": features });
  }

  geoJsonToLeaflet() {
    const app = this;
    const components = this.state.features
      .map((feature) => { return feature.geometry; })
      .filter(geoIsValid)
      .map(function(geo, i) {
        const geotype = geo.type
        const color = (i === app.state.activeFeature) ? "#FF0033" : "#3388ff";
        let component;
        switch (geotype) {
          case "Point":
            component = <Circle
                          center={[geo.coordinates[1], geo.coordinates[0]]}
                          radius={10}
                          color={color}
                        />;
            break;
          case "LineString":
            {
              const positions = geo.coordinates.map((cx, i) => {
                return [cx[1], cx[0]];
              });
              component = <Polyline positions={positions} color={color} />;
            }
            break;
          case "Polygon":
            {
              const positions = geo.coordinates.map((ring) => {
                return ring.map((cx) => { return [cx[1], cx[0]]; });
              });
              component = <Polygon positions={positions} color={color} />;
            }
            break;
          case "MultiPoint":
            component = geo.coordinates.map((cx, i) => {
              return <Circle
                        center={[cx[1], cx[0]]}
                        radius={10}
                        key={i}
                        color={color}
                      />;
            });
            break;
          default:
            throw new Error("unhandled geometry type: " + geotype);
        }
        return component;
    })
    return components;
  }

  render() {
    return (
      <div className="App">
        <div>
          <div style={{width: "75%", float: "left"}}>
            <Map
              center={mapCenter}
              zoom={zoomLevel}
              onClick={this.handleMapClick}
              >
              <TileLayer
                attribution={tileAttr}
                url={tileUrl}
                id={id}
                accessToken = {accessToken}
              />
              {this.geoJsonToLeaflet()}
            </Map>
          </div>
          <div style={{width: "25%", float: "right"}}>
            <div id={"controls"}>
              <GeometryTypeSelector
                value={this.state.nextGeometryType}
                handleSelect={this.setNextGeometryType}
              />
              <button className="std" onClick={this.addFeature}>{"Create"}</button>
            </div>
            <FeatureList
              features={this.state.features}
              activeIdx={this.state.activeFeature}
              handleClick={this.handleFeatureClickFactory}
              deleteFunc={this.deleteFeature}
            />
            <div>
              <PropertyEditor
                feature={this.activeFeature()}
                setProperty={(key, val, oldkey) => {this.setFeatureProperty(this.state.activeFeature, key, val, oldkey)}}
              />
            </div>
            <SourceWindow feature={this.activeFeature()} />
            <div>
              {/* <button className="ui">{"Share"}</button> */}
              {/* <button className="ui">{"Export"}</button> */}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



